import React, { useState } from 'react';
import { FaPaperPlane } from 'react-icons/fa';
import user from './image/user.png';
import vwn from './image/ai.png';
import cross from './image/cross.png';
import pc from './image/pc.png';
import android from './image/android.png';
import worldData from './worldData';
import './AI.css';


const App = () => {
  const [isChatEnabled, setIsChatEnabled] = useState(true)
  const [question, setQuestion] = useState('');
  const [messages, setMessages] = useState([
    {sender: "bot", message: "こんにちは！VRChat World Naviです。VRChatに投稿されているワールドについて質問することができます。", worlds:[]}
  ]);

  const chatTimer = () => {
    setIsChatEnabled(true);
  };

  const handleChange = (e) => {
    setQuestion(e.target.value);
  };

  const updateMessages = (data) => {
    setMessages(prevMessages => {
      const lastMessage = prevMessages[prevMessages.length - 1].message;
      const updatedMessage = lastMessage ? lastMessage + data.message : data.message;
      return [...prevMessages.slice(0, -1), {sender: data.sender, message: updatedMessage, worlds: []}];
    });
  };

  const updateWorlds = (data) => {
    const parsedData = JSON.parse(data.message);
    const answer = parsedData.answer;
    const worldIds = searchWorldId(answer);
    console.log(worldIds);
    const worlds = searchWorld(worldIds);
    console.log(worlds);
    setMessages(prevMessages => {
      const lastMessage = prevMessages[prevMessages.length - 1].message;
      return [...prevMessages.slice(0, -1), {sender: data.sender, message: lastMessage, worlds: worlds}];
      //return [...prevMessages.slice(0, -1), {sender: data.sender, message: lastMessage, worlds: worldData}];
    });
  };

  const searchWorldId = (answer) => {
    let startIndex = answer.indexOf("wrld");
    const worldIds = [];
    while (startIndex !== -1) {
      const worldId = answer.substring(startIndex, startIndex + 41);
      worldIds.push(worldId);
      startIndex = answer.indexOf("wrld", startIndex + 1);
    }
    return worldIds;
  };

  const searchWorld = (worldIds) => {
    return worldIds.map((worldId) => {
      for (let i = 0; i < worldData.length; i++) {
        if (worldData[i].id === worldId) {
          return worldData[i];
        }
      }
      return null;
    });
  };

  const createWorldURL = (world_id) => "https://vrchat.com/home/world/" + world_id;

  const checkPlatform = (plat) => {
    if (plat === "cross"){
      return cross
    }
    else if (plat === "pc"){
      return pc
    }
    else if (plat === "android"){
      return android
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    setMessages(prevMessages => [...prevMessages, {sender: 'user', message: question, worlds:[]}]);
    window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
    
    if (isChatEnabled){
      if (question.length > 9){
        const apiUrl = `wss://api.docsbot.ai/teams/83TgCli7OejrM8SX14db/bots/GA2Sc3AbdQSDy8EX6VWa/chat`;
        const ws = new WebSocket(apiUrl);
    
        ws.onopen = function (event) {
          const req = { question: question, full_source: false, history: [] };
          ws.send(JSON.stringify(req));
        };   
        setQuestion('');
    
        ws.onmessage = function (event) {
          const data = JSON.parse(event.data);
          if (data.sender === 'bot') {
            if (data.type === 'start') {
              setMessages(prevMessages => [...prevMessages, {sender: data.sender, message: data.message, worlds:[]}]);
              window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
            } else if (data.type === 'stream') {
              updateMessages(data);
              window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
            } else if (data.type === 'end') {
              updateWorlds(data);
              window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
              ws.close();
              setIsChatEnabled(false);
              setTimeout(chatTimer, 30000);
            } else if (data.type === 'error') {
              setMessages(prevMessages => [...prevMessages, {sender: "bot", message: "エラーが発生しました。もう一度質問してください。何回もエラーが発生する場合は、管理者に問い合わせてください。", worlds:[]}]);
              ws.close();
            }
          }
        };
    
        ws.onerror = function (event) {
          setMessages(prevMessages => [...prevMessages, {sender: "bot", message: "エラーが発生しました。もう一度質問してください。何回もエラーが発生する場合は、管理者に問い合わせてください。", worlds:[]}]);
          console.log('Connection Error');
        };
    
        ws.onclose = function (event) {
          if (!event.wasClean) {
            setMessages(prevMessages => [...prevMessages, {sender: "bot", message: "エラーが発生しました。もう一度質問してください。何回もエラーが発生する場合は、管理者に問い合わせてください。", worlds:[]}]);
            console.log('Network Error');
          }
        };
      }
      else {
        setMessages(prevMessages => [...prevMessages, {sender: "bot", message: "メッセージの文字数が短すぎます。もう一度質問してください。", worlds:[]}]);
        window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
      };
    }
    else {
      setMessages(prevMessages => [...prevMessages, {sender: "bot", message: "連続してメッセージを送信することはできません。しばらくしてからもう一度質問してください。", worlds:[]}]);
      window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
    };
  };

  return (
    <div class="container">
      <div className="chat-body">
        {messages.map((message, index) => (
          <div className="chat" key={index}>
            {message.sender === "user" ? (
              <div className="user-chat">
                <img className="chat-image" src={user} alt="user" />
                <h4 className="chat-message">{message.message}</h4>
              </div>
            ) : (
              <div className="bot-chat">
                <div className="bot-chat-message">
                  <img className="chat-image" src={vwn} alt="vwn" />
                  <h4 className="chat-message">{message.message}</h4>
                </div>
                <div className="world-body">
                  {message.worlds.map((world, worldIndex) =>
                    world !== null ? (
                      <div key={worldIndex} className="world-chat">
                        <div className="world-image">
                          <a href={createWorldURL(world.id)} target="_blank" rel="noreferrer">
                            <img src={world.url} alt={world.name} className="main-image" />
                          </a>
                          <img src={checkPlatform(world.plat)} alt="" className="overlay-image" />
                          <p className="world-name">{world.name}</p>
                        </div>
                      </div>
                    ) : (
                      <div></div>
                    )
                  )}
                </div>
              </div>
            )}
          </div>
        ))}
      </div>
      <form className="chat-footer" onSubmit={handleSubmit}>
        <input type="text" className="user-input" placeholder="メッセージを入力してください" value={question} onChange={handleChange} />
        <button className="send-button" type="submit">
          <FaPaperPlane />
        </button>
      </form>
    </div>
  );
};

export default App;